/* ğŸŒŸ ØªÙ†Ø³ÙŠÙ‚ Ø¹Ø§Ù… Ù„Ù„ØªØ·Ø¨ÙŠÙ‚ */
body, .app {
  font-family: "Tajawal", sans-serif;
  direction: rtl;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  color: #222;
}

.app.dark {
  background-color: #1a1a1a;
  color: #eee;
}

header {
  text-align: center;
  padding: 20px;
}

header h1 {
  margin-bottom: 10px;
}

/* ğŸ”˜ Ø§Ù„Ø£Ø²Ø±Ø§Ø± ÙˆØ§Ù„ØªØ¨ÙˆÙŠØ¨Ø§Øª */
button {
  background-color: #1e73ff;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 16px;
  cursor: pointer;
  font-size: 15px;
}

button:hover {
  opacity: 0.9;
}

.tabs {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  margin: 10px auto;
}

/* ØªØ¨ÙˆÙŠØ¨ Ù†Ø´Ø· */
.tabs button.active {
  background-color: #0b5ed7;
  box-shadow: 0 0 0 2px rgba(30, 115, 255, 0.3);
}

.panel {
  background: white;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 0 10px rgba(0,0,0,0.08);
}

/* ğŸ“¦ ØªÙ†Ø³ÙŠÙ‚ Ø¥Ø¯Ø®Ø§Ù„Ø§Øª Ø§Ù„Ù…Ù†ØªØ¬Ø§Øª */
input {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  outline: none;
}

.panel input[type="file"] {
  background: #fff;
  padding: 10px;
  border-radius: 10px;
  border: 1px dashed #aaa;
}

/* ğŸ§¾ Ø§Ù„Ø¬Ø¯Ø§ÙˆÙ„ */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

th {
  background-color: #1e73ff;
  color: white;
}

/* ğŸ§° ÙƒØ±ÙˆØª Ø§Ù„Ù…Ù†ØªØ¬Ø§Øª */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
  margin-top: 20px;
}

.product {
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.08);
  padding: 10px;
  text-align: center;
}

.product img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 6px;
}

/* âœ¨ Ù†ØµÙˆØµ ØµØºÙŠØ±Ø© Ø¨Ø§Ù‡ØªØ© */
.muted {
  color: #777;
  font-size: 13px;
}

/* ğŸ–¨ï¸ Ø²Ø± Ø§Ù„Ø­ÙØ¸ */
.save-btn {
  background-color: #28a745;
}
.save-btn:hover {
  opacity: 0.85;
}

/* ğŸ“‡ ÙƒØ±ÙˆØª Ø§Ù„Ø¨Ø§Ø±ÙƒÙˆØ¯ */
.barcode-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
  gap: 10px;
  margin-top: 10px;
}

.barcode-card {
  background: #fff;
  border-radius: 10px;
  padding: 8px;
  text-align: center;
  box-shadow: 0 1px 4px rgba(0,0,0,0.08);
}

.barcode-card img {
  margin-top: 6px;
}

/* ===== Ù„Ù„Ø·Ø¨Ø§Ø¹Ø© ÙÙ‚Ø· ===== */
@media print {
  header, nav, footer, .tabs, .panel, button, input, .muted {
    display: none !important;
  }

  body, .app, main, section.invoice, section.barcodes {
    background: #fff !important;
    color: #000 !important;
    margin: 0;
    padding: 0;
  }

  table {
    width: 100%;
    border-collapse: collapse;
  }

  table th, table td {
    border: 1px solid #aaa;
    padding: 6px;
    text-align: center;
  }

  h1, h2, h3 {
    text-align: center;
  }

  /* Ø¥Ø¸Ù‡Ø§Ø± ÙÙ‚Ø· Ø§Ø³Ù… Ø§Ù„Ù†Ø¸Ø§Ù… ÙˆØ§Ù„ØªØ§Ø±ÙŠØ® ÙÙŠ Ø§Ù„ÙØ§ØªÙˆØ±Ø© */
